
body {
  margin: 0;
  font: 36px/1.5 Helvetica;
}
body,
html {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}
a {
  text-decoration: none;
  color: #4f4e4e;
}
ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
.pl20 {
  padding-left: 20px;
}
.center {
  margin: 0 auto;
  width: 1098px;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.overhidden {
  overflow: hidden;
}
p {
  margin: 0;
}
img {
  vertical-align: top;
  border: none;
}
button {
  -webkit-appearance: none;
  border-radius: 0;
}
a {
  text-decoration: none;
}
* {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
/*ios android去除自带阴影的样式*/
}
ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}
dl,
dd {
  margin: 0;
  padding: 0;
}
i,
em {
  font-style: normal;
}
strong {
  font-weight: normal;
}
.current {
  color: #f05848;
}
.text-center {
  text-align: center;
}
.p45 {
  padding: 0 2.25rem;
}
input {
  border: none;
  padding: 0;
  margin: 0;
}
button {
  border: none;
  padding: 0;
  margin: 0;
}
.icon {
  display: inline-block;
  overflow: hidden;
  background: url("./assets/imgs/icon.png");
  background-size: 55.6rem 42.95rem;
}


